import React, { useEffect, useState } from "react";
import { Modal, Button, } from "react-bootstrap";
import firebase from 'firebase'
import { connect } from 'react-redux';
import style from './Basket.module.css'
import delItemImg from '../../Assets/img/basketCartDel.png'
import { NavLink } from "react-router-dom";
const Basket = (props) => {
    const [items, setItems] = useState([])
    const [allSum, setAllSum] = useState()

    useEffect(async () => {
        const arr = []
        let s = 0
        for (let i = 0; i < props.orders.length; i++) {
            const resp = await firebase.firestore().collection("product").doc(props.orders[i].id).get()
            arr.push({ ...resp.data(), id: i + 1, uid: props.orders[i].id, quantity: props.orders[i].quantity })
            s += resp.data().price*props.orders[i].quantity
        }
        setItems(arr)
        setAllSum(s)
        console.log(arr)
    }, [props.orders])
    const delPr = (e) => {
        const washingtonRef = firebase.firestore().collection("users").doc(props.uid)
        washingtonRef.update({
            orders: firebase.firestore.FieldValue.arrayRemove({ id: e.target.name })
        });
    }
    function updateArray(arr, id, numb) {
        for (let i = 0; i < arr.length; i++) {
            if (arr[i].id === id) arr[i] = { id: arr[i].id, quantity: arr[i].quantity + numb }
            else arr[i] = { id: arr[i].id, quantity: arr[i].quantity }
        } return arr
    }
    const increment = async (e) => {
        const ref = await firebase.firestore().collection("users").doc(props.uid).get()
        const arr = ref.data().orders
        updateArray(arr, e.target.name, +1)
        const washingtonRef = firebase.firestore().collection("users").doc(props.uid)
        washingtonRef.update({
            orders: arr
        });
    }
    const decrement = async (e) => {
        const ref = await firebase.firestore().collection("users").doc(props.uid).get()
        const arr = ref.data().orders
        updateArray(arr, e.target.name, -1)
        const washingtonRef = firebase.firestore().collection("users").doc(props.uid)
        washingtonRef.update({
            orders: arr
        });
    }

    const buySuccess = (e) => {
        console.log(e)

        props.onHide()
    }
    return (
        <Modal
            {...props}
            size="lg"
            aria-labelledby="contained-modal-title-vcenter"
            centered >
            <Modal.Header closeButton>
                <Modal.Title id="contained-modal-title-vcenter">
                    Basket
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <div>
                    {items.length === 0 ? <h4>Кошик порожній</h4> :
                        items.map((p) => {
                            console.log(p)
                            return (
                                <div>
                                    <div className={style.productBody}>
                                        {/* <h2>{p.id}</h2> */}
                                        <img src={p.productImage} style={{ height: '200px' }} />
                                        <h5>Велосипед {p.brendName}</h5>
                                        <button type="button" onClick={delPr} name={p.uid}
                                            style={{ margin: 'auto', height: '50px', width: '50px' }}>
                                            <img name={p.uid} src={delItemImg} alt="" style={{ margin: 'auto', height: '40px' }} />
                                            Видалити
                                        </button>
                                    </div>
                                    <p>Ціна: {p.price}</p>
                                    <div className={style.productFootee}>
                                        <div className={style.productCounter}>
                                            <button onClick={decrement} name={p.uid}>−</button>
                                            <input type="text" name="" disabled value={p.quantity} />
                                            <button onClick={increment} name={p.uid}>+</button>
                                        </div>

                                        <b style={{ float: 'right' }}>Сума: {p.price*p.quantity} <b>₴</b></b>
                                        <br />
                                    </div>
                                    <hr />
                                </div>
                            )
                        })
                    }
                    <b style={{ float: 'right' }}>Загалом: {allSum} <b>₴</b></b>
                </div>
            </Modal.Body>
            <Modal.Footer>
                <NavLink onClick={buySuccess} className="btn btn-success nav-link " to={"/order"}>Оформити замовлення </NavLink>

                {/* <Button onClick={props.onHide} > </Button> */}
            </Modal.Footer>
        </Modal>
    )
}
const mapStateToProps = (state) => ({
    uid: state.firebase.auth.uid,
})
export default connect(mapStateToProps, {})(Basket)